* {
  font-family: sans-serif;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  
}

body{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: url('./bg4.jpg') no-repeat center center/cover; 
  height: 100vh;
}

input,
button {
  font-size: 20px;
  padding: 10px;
  border-radius: 5px;
}

input {
  display: block;
  margin-bottom: 20px;
}

button {
  border: 1px solid gray;
  background: whitesmoke;
  margin-right: 5px;
  cursor: pointer;
}

button:nth-last-child(2),
button:nth-last-child(1) {
  background: tomato;
  color: white;
  border-radius: 5px; 
}

button:hover{
  background-color: black;
  color:white;
}

button:nth-last-child(2):hover,
button:nth-last-child(1):hover{
  background-color: whitesmoke;
  color:black;
}
#root{
  width: 500px;
  background: rgba(255, 255, 255, 0.1);
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  padding: 30px 20px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  
}
.App{
  display: flex;
  flex-direction: column;
    justify-content: center;
    align-items: center;
}
.buttons{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 10px;
  width:300px;
} 

h1{
  font-size: 2.4rem;
  text-align: center;
}

p{
  padding-top: 20px;
}

@media screen and (max-width: 500px){
  #root{
    width: 90%;
  }
  
  h1{
    font-size: 2rem;
  }
}
